import React, { useState } from "react";

import DatalineQuotesApexChart from "./DatalineQuotesApexChart";

import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Table from "react-bootstrap/Table";

function makeDataSeries(quotesArray, chart_name) {
  return quotesArray.map((item) => {
    return [item.quoteDate, item[chart_name]];
  });
}

export default function DatalineQuotesApexChartEditor(props) {
  const [quoteDate, setQuoteDate] = React.useState("");
  const [chart1, setChart1] = React.useState('');
  const [chart2, setChart2] = React.useState('');
  const [chart3, setChart3] = React.useState('');
  const [chart4, setChart4] = React.useState('');
  const [quotesArray, setQuotesArray] = React.useState([]);

  const [showChart, setShowChart] = React.useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();

    if (quoteDate !=='' && !!quoteDate) {
      let timestamp = new Date(quoteDate).getTime();
      setQuotesArray((quotesArray) => [
        ...quotesArray,
        { quoteDate: timestamp, chart1, chart2, chart3, chart4 },
      ]);
      setQuoteDate('');
      setChart1('');
      setChart2('');
      setChart3('');
      setChart4('');
      console.log(quotesArray);

      let series = [
        { name: "Факт", data: makeDataSeries(quotesArray, "chart1") },
        { name: "Стратегия 1", data: makeDataSeries(quotesArray, "chart2") },
        { name: "Стратегия 2", data: makeDataSeries(quotesArray, "chart3") },
        { name: "Стоп лосс", data: makeDataSeries(quotesArray, "chart4") },
      ];
    
      props.setChartSeries(series)

    }    
  };

  let series = [
    { name: "Факт", data: makeDataSeries(quotesArray, "chart1") },
    { name: "Стратегия 1", data: makeDataSeries(quotesArray, "chart2") },
    { name: "Стратегия 2", data: makeDataSeries(quotesArray, "chart3") },
    { name: "Стоп лосс", data: makeDataSeries(quotesArray, "chart4") },
  ];

  console.log(series);

  return (
    <div>
      <div>Dataline Quotes ApexChart Options Editor</div>

      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="quoteDate">
          <Form.Label size="sm">Дата</Form.Label>
          <Form.Control
            type="date"
            placeholder="Дата"
            size="sm"
            value={quoteDate}
            onChange={(e) => setQuoteDate(e.target.value)}
          />
          <Form.Text className="text-muted">Дата котировки</Form.Text>
        </Form.Group>

        <Form.Row>
          <Form.Group controlId="chart1" as={Col}>
            <Form.Label size="sm">График 1</Form.Label>
            <Form.Control
              type="number"
              placeholder="quote"
              size="sm"
              value={chart1}
              onChange={(e) => setChart1(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="chart2" as={Col}>
            <Form.Label size="sm">График 2</Form.Label>
            <Form.Control
              type="number"
              placeholder="quote"
              size="sm"
              value={chart2}
              onChange={(e) => setChart2(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="chart3" as={Col}>
            <Form.Label size="sm">График 3</Form.Label>
            <Form.Control
              type="number"
              placeholder="quote"
              size="sm"
              value={chart3}
              onChange={(e) => setChart3(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="chart4" as={Col}>
            <Form.Label size="sm">График 4</Form.Label>
            <Form.Control
              type="number"
              placeholder="quote"
              size="sm"
              value={chart4}
              onChange={(e) => setChart4(e.target.value)}
            />
          </Form.Group>
        </Form.Row>

        <Button variant="secondary" type="submit">
          Добавить
        </Button>
      </Form>
      <ShowTable table_data={quotesArray} />
      <Button
        variant="outline-secondary"
        onClick={() => setShowChart((showChart) => !showChart)}
      >
        График
      </Button>
      {showChart ? <DatalineQuotesApexChart 
      series={series}
      options={{stroke: { curve: 'smooth' }}}
        /> : null}
    </div>
  );
}

function ShowTable(props) {
  return (
    <>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Дата</th>
            <th>chart1</th>
            <th>chart2</th>
            <th>chart3</th>
            <th>chart4</th>
          </tr>
        </thead>
        <tbody>
          {props.table_data.map((quote, index) => (
            <tr key={index}>
              <td>{quote.quoteDate}</td>
              <td>{quote.chart1}</td>
              <td>{quote.chart2}</td>
              <td>{quote.chart3}</td>
              <td>{quote.chart4}</td>
            </tr>
          ))}
        </tbody>
      </Table>
    </>
  );
}
