import React, { useEffect, useState } from "react";

import UniFormLayout from "./UniFormLayout";


// import SVGSmileRegular from "../svgs/SVGSmileRegular.js";
// import SVGSmileFrownRegular from "../svgs/SVGSmileFrownRegular.js";
import SVGSmileNeutral from "../svgs/SVGSmileNeutral.js";
import SVGBootstrapIconEmojiAngry from "../svgs/SVGBootstrapIconEmojiAngry.js";
import SVGBootstrapIconEmojiSmile from "../svgs/SVGBootstrapIconEmojiSmile.js";

import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import FormControl from 'react-bootstrap/FormControl';
import InputGroup from 'react-bootstrap/InputGroup';

// const schema = {
//     title: 'Исходные данные',
//     type: 'object',
//     properties: {
//         debt: { label: 'Доля долга', type: 'number', minimum: 0, maximum: 100 },
//         costOfDebt: { label: 'Стоимость долга, % годовых', type: 'number', minimum: 0, maximum: 20 }
//     },
//     required: ['debt', 'costOfDebt']
// };


export default function CaseFinancialLeverage() {

    const [yearProfit, setYearProfit] = useState(20);
    const [equityReturnsArray, setEquityReturnsArray] = useState([]);
    const [debt, setDebt] = useState(0);
    const [costOfDebt, setCostOfDebt] = useState(10);


    useEffect(() => {
        const interval = setInterval(() => {
            let profit = (Math.floor(Math.random() * 50 * 10) - 10) / 10;
            setYearProfit(profit);
            setEquityReturnsArray(equityReturnsArray => [...equityReturnsArray, profit]);
        }, 2000);
        return () => clearInterval(interval);
    }, []);

    function handleSubmit(object) {
        //    setParamsObject(object);
    }

    let equity = 100 - parseFloat(debt);
    let equityProfit = yearProfit - parseFloat(debt) * parseFloat(costOfDebt) / 100;
    let equityProfitability = equityProfit * 100 / equity;

    return <Card bg={"light"} style={{ width: '95%', margin: '1rem' }}>
        <Card.Header>Симулятор</Card.Header>
        <Card.Body>
            <Card.Title>Финансовый рычаг и риски</Card.Title>

            <InputGroup size="sm" className="mb-1">
                <InputGroup.Prepend>
                    <InputGroup.Text id="inputGroup-sizing-sm">Долг</InputGroup.Text>
                </InputGroup.Prepend><FormControl
                    aria-label="Small" aria-describedby="inputGroup-sizing-sm"
                    type="number"
                    value={debt}
                    onChange={e => setDebt(e.target.value)} />
            </InputGroup>

            <InputGroup size="sm" className="mb-1">
                <InputGroup.Prepend>
                    <InputGroup.Text id="inputGroup-sizing-sm">Стоимость долга</InputGroup.Text>
                </InputGroup.Prepend><FormControl
                    aria-label="Small" aria-describedby="inputGroup-sizing-sm"
                    type="number"
                    value={costOfDebt}
                    onChange={e => setCostOfDebt(e.target.value)} />
            </InputGroup>



            <Container>
                <Row>
                    <Col>Активы</Col>
                    <Col>Пассивы</Col>
                    <Col>Инвесторы</Col>
                    <Col>Рент</Col>
                    <Col>Оценка</Col>
                </Row>
                <Row>
                    <Col>100</Col>
                    <Col>СК {equity}</Col>
                    <Col>{Math.floor(equityProfit * 10) / 10}</Col>
                    <Col>{Math.floor((equityProfit) * 100 * 10 / equity) / 10}</Col>
                    <Col>
                        {equityProfitability < parseFloat(costOfDebt) && <SVGBootstrapIconEmojiAngry width="30" height="30" />}
                        {equityProfitability > parseFloat(costOfDebt) && <SVGBootstrapIconEmojiSmile width="30" height="30" />}
                    </Col>
                </Row>
                <Row>
                    <Col>-</Col>
                    <Col>Долг {debt}</Col>
                    <Col>{debt * costOfDebt / 100}</Col>
                    <Col>{costOfDebt}</Col>
                    <Col><SVGSmileNeutral width="30" height="30" /></Col>
                </Row>
            </Container>
            <Card.Text>
                Прибыль {Math.floor(yearProfit * 10) / 10}
            </Card.Text>
        </Card.Body>
    </Card>
}

